
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';
import validateForm from '@/utils/validators/validate-form';
import isFilled from '@/utils/validators/is-filled';
import strLength from '@/utils/validators/str-length';
import { Form } from 'element-ui';
import {
  defineComponent, watch, onMounted, reactive,
} from '@vue/composition-api';
import i18n from 'i18next';
import VueInputTag from '../vue-input-tag.vue';
import { CustomInput } from './OrderSamples.vue';

class SamplePrice {
  samplePrice = '';
  samplePriceUnit = '';
  samplePriceNumber = '';
}

class ValueWithUnit {
  value = '';
  unit = '';
}

export class Material {
  materialName = ''; // 材料系列名称
  materialNumber = ''; // 材料系列编号
  materialType: number | string = ''; // 材质类型
  supplierArticleName = ''; // 材质类型编号
  labels = [] as string[];
  supplierArticleNo = ''; // 材料色号
  samplePrice = new SamplePrice();
  supplierName = '';
  partUsed = '';
  moq = new ValueWithUnit();
  season = '';
  colorFamily = '';
  color = '';
  samplePeriod = new ValueWithUnit();
  productionLeadTime = new ValueWithUnit();
  specification = '';
  thickness = new ValueWithUnit();
  materialCountryOfOrigin = '';
  notes = '';
  shape = [];
  public modifyDesc: string[] = ['']; // 修改说明, 长度至少为1
  public customInputs: CustomInput[] = [new CustomInput(), new CustomInput(), new CustomInput(), new CustomInput(), new CustomInput()];
  public dataReceiviedTime: number | undefined = undefined; // 资料收到日期
  public deadline: number | undefined = undefined; // 单款交期
  public bindOrderNo: string = ''; // 客户单号,

  did = `${+new Date()}${Math.floor(Math.random() * 1000)}`;
  constructor() {
    return this;
  }
}

function checkDuplicateMaterialName(val: string, materials: Material[]) {
  const length = materials.filter(i => i.materialName === val && val !== '')?.length || 0;
  if (length >= 2) throw new Error('材料系列名称不能重复');
}


