const orderMaterials = defineComponent({
  props: {
    materials: {
      type: Array as unknown as () => Material[],
      default: () => {
        return [new Material()];
      },
    },
    typeOptions: {
      type: Array as unknown as () => { key: string, value: string, label: string }[],
      default: [],
    },
    disable: {
      type: Boolean,
      default: false,
    },
    status: {
      type: Number,
      default: null,
    },
  },
  render() {
    const s = this as InstanceType<typeof orderMaterials>;
    return (
      <div class=''>
        {
          (s.materials).map((item, index) => {
            return (
              <transition name='fade' appear>
                <div>
                  <el-form key={item.did} ref={`item-${index}`} props={{ model: item }}
                    rules={this.rules} inline={true} class='flex'
                    style={{
                      position: 'relative',
                    }}>

                    <el-form-item style={{
                      display: 'flex!important',
                      flexDirection: 'column',
                      alignItems: 'end',
                    }}
                      label={i18n.t('order_management.form.collection') + (index + 1)} required prop="materialName" key={index}>
                      <el-input disabled={s.disable} size='small'
                        style={{ width: '350px', marginRight: '15px' }}
                        placeholder='' value={item.materialName} oninput={(value: string) => { item.materialName = value; }} />
                    </el-form-item>

                    <el-form-item style={{
                      display: 'flex!important',
                      flexDirection: 'column',
                      alignItems: 'end',
                    }} label={i18n.t('order_management.form.material_type')} required prop="materialType">
                      <el-select disabled={s.disable} size='small'
                        vModel={item.materialType} style={{ width: '350px', marginRight: '15px' }}>
                        {
                          (s.typeOptions || []).map(type => <el-option key={type.key}
                            value={type.value} label={type.label}></el-option>)
                        }
                      </el-select>
                    </el-form-item>

                    <el-form-item style={{
                      display: 'flex!important',
                      flexDirection: 'column',
                      alignItems: 'end',
                    }} label={i18n.t('order_management.form.tag')}>
                      <div class="flex-center" style={{
                        height: '37px',
                      }}>
                        <VueInputTag placeholder='' limit={1} readOnly={s.disable}
                          style={{ height: '32px', width: '330px' }} innerTags={item.labels} />
                      </div>
                    </el-form-item>

                    {
                      // 增删按钮
                    }
                    <div vShow={!s.disable} class='flex-vertical-center' style={{
                      height: '32px',
                      width: '70px',
                      marginTop: '26px',
                    }}>
                      <div class="iconfont iconshanchu1" style={{
                        color: '#666',
                        marginRight: '12px',
                        cursor: 'pointer',
                      }} vShow={this.isAbleToDelete(index)}
                        onclick={() => s.deleteItem(index)}></div>

                      <div class="iconfont icontianjia" style={{
                        cursor: 'pointer',
                        marginRight: '12px',
                        color: '#666',
                      }} vShow={s.materials.length - 1 === index}
                        onclick={() => s.materials.push(new Material())}></div>
                      <div class="iconfont iconfuzhi" style={{
                        cursor: 'pointer',
                        color: '#666',
                      }}

                        onclick={() => s.materials.push(this.copyMaterial(item))}></div>
                    </div>
                  </el-form>
                  {
                    <div style={{
                      bottom: '18px',
                      width: '100%',
                      marginBottom: index === s.materials.length - 1 ? 0 : '20px',
                      marginTop: '4px',
                      zIndex: '1',
                    }} class='flex-vertical-center flex-space-between'>
                      <div class="text-link">
                        <i class="iconfont icondankuanxiangqing-24px" style={{ marginRight: '2px' }} />
                        <span class="text-link" onclick={() => { this.openMaterialSample(index); }}>
                          {i18n.t('order_management.material_dynamic_form.collection_detail')}</span>
                      </div>
                    </div>
                  }
                </div>
              </transition>
            );
          })
        }
        {
          // <el-button onclick={() => s.validateForms.call(s)}>validate</el-button>
        }
      </div>
    );
  },
  setup(props) {
    onMounted(() => {
      if (!props.materials.length) {
        props.materials.push(new Material());
      }
    });
    const originLength = props.materials.length;

    const rules = reactive({
      materialType: validator2ElemRule(isFilled),
      materialName: validator2ElemRule((value: string) => {
        isFilled(value);
        strLength(value, { min: 1, max: 64 });
        checkDuplicateMaterialName(value, props.materials);
      }),
    });

    function deleteItem(this: InstanceType<typeof orderMaterials>, index: number) {
      this.materials.splice(index, 1);
    }
    function validateForms(this: InstanceType<typeof orderMaterials>) {
      return Promise.all(this.materials.map(async (_, i) => {
        await validateForm(this.$refs[`item-${i}`] as Form);
      }));
    }
    function openMaterialSample(index: number) {
      const target = props.materials[index];
      const { did } = target;
      Object.assign(window, {
        [`material_${did}`]: target,
      });
      const disableEditFlag = `${props.disable ? '&f2=1' : ''}`;
      window.open(
        `${window.location.origin}/t/material-order-form?id=${did}${disableEditFlag}`,
      );

      // const { detail } = target;
      // Object.assign(window, {
      //   [`sample_${detail.id}`]: target,
      // });
      // const disableUploadFlag = `${props.disableUpload ? '&f=1' : ''}`;
      // const disableEditFlag = `${props.disableEdit ? '&f2=1' : ''}`;
      // detail.page = window.open(
      //   `${window.location.origin}/t/sample-order-form?id=${detail.id}${disableUploadFlag}${disableEditFlag}`,
      // );
      // console.log('open sample form');
    }
    function isAbleToDelete(index: number) {
      if (props.status !== null) {
        return index > originLength - 1 && props.materials.length !== 1;
      }
      return props.materials.length !== 1;
    }

    function copyMaterial(item: Material) {
      const oldObj = JSON.parse(JSON.stringify(item, (key, value) => {
        if (key === 'materialName') {
          return null;
        }
        return value;
      }));
      const newObj = new Material();
      Object.assign(newObj, oldObj, { did: `${+new Date()}${Math.floor(Math.random() * 1000)}` });
      return newObj;
    }

    return {
      deleteItem,
      validateForms,
      isAbleToDelete,
      openMaterialSample,
      copyMaterial,
      rules,
    };
  },
});
export default orderMaterials;
