































































import {
  defineComponent, ref, watch,
} from '@vue/composition-api';
import { ElForm } from 'element-ui/types/form';
import i18n from 'i18next';
import { validateFormFields } from '@/utils/validators/rules-validation';
import { basicFormRules } from '../rules';


export default defineComponent({
  props: {
    order: {
      type: Object,
      default: () => {
        return {
          companyName: '',
          type: 1,
          time: '',
          season: '',
          brand: '',
          category: '',
          date: '',
        };
      },
    },
    orderNo: {
      type: String,
    },
    disable: {
      type: Boolean,
      default: false,
    },
    typeDisabled: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const formTitle = ref('');
    const startTime = ref('00:00');
    watch(
      () => props.order.date,
      (date) => {
        if (new Date(date).toDateString() === new Date().toDateString()) {
          const hours = new Date().getHours();
          if (hours === 23) {
            startTime.value = `${hours.toString().padStart(2, '0')}:00`;
          } else {
            startTime.value = `${(hours + 1).toString().padStart(2, '0')}:00`;
          }
        } else {
          startTime.value = '00:00';
        }
      }, { immediate: true },
    );
    watch(
      () => props.orderNo,
      (orderNo) => {
        if (orderNo) {
          formTitle.value = `${i18n.t('order_management.form.order')}${orderNo}:`;
        } else {
          formTitle.value = i18n.t('order_management.form.create_order');
        }
      }, { immediate: true },
    );
    // 表格的vue实例
    const basicForm = ref<ElForm>();
    // 验证规则
    const rules = basicFormRules;

    const validateDate = (date: Date) => {
      return (new Date(date)).getTime() <= (new Date()).getTime() - (24 * 60 * 60 * 1000);
    };

    // 外部调用, 返回需求的表单信息
    const validateForm = async () => {
      if (basicForm.value) {
        await validateFormFields(basicForm?.value);
      }
    };
    return {
      basicForm,
      rules,
      validateForm,
      formTitle,
      validateDate,
      startTime,
    };
  },

});
