<template>
  <section class="flex-vertical-center order-form" v-loading="loading">
    <form-header
      :title="orderNo"
      :order="order"></form-header>
    <div class="form-container">
      <!-- 基础表格 -->
      <basic-form ref="basicForm" :order="order" :orderNo="orderNo" :disable="isOrderSubmited" :typeDisabled="order.status !== null"></basic-form>
      <!-- 分割线 -->
      <div class="split"></div>
      <!-- dynamic form 材质订单 -->
      <order-materials
        v-if="order.type === 1 && (!$route.query.orderNo || $route.query.orderNo && order.status)"
        :status="order.status"
        :materials="order.form"
        :disableUpload="order.status === 6"
        :typeOptions="options"
        ref="dynamicForm"
        :disable="isOrderSubmited" />
      <order-samples
        v-if="order.type === 2 && (!$route.query.orderNo || $route.query.orderNo && order.status)"
        :samples="order.form"
        :status="order.status"
        ref="dynamicForm"
        :disable="isOrderSubmited"
        :disableUpload="order.status === 6"
        :disableEdit="isOrderSubmited > 1" />
      <!-- 分割线 -->
      <div class="split"></div>
      <!-- 备注 -->
      <el-form :model="order" size="medium" inline
        :label-width="labelWidth"
        label-position="top">
        <el-form-item :label="$t('order_management.form.remark')" class="shidi-form-item">
          <el-input type="textarea" :rows="4" resize="none" :disabled="isOrderSubmited"
            maxlength="500" v-model='order.remark' show-word-limit :style="remarkWidth"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="form-container"
      v-if="($route.query.orderNo && order.status) || !$route.query.orderNo">
      <h3 :style='{paddingBottom:"20px"}'>{{$t('order_management.form.attachment')}}</h3>
      <Uploader class='uploader' :maxSize="500" :uploadedFiles="order.attachment"
        :disable="order.status && order.status===6"
        :disableUpload="order.status === 6" />
    </div>
    <div class="form-submit-section flex-justify-flex-end flex-vertical-center"
      v-if="order.status === null || order.status !== 6">
      <div v-if="showUpdateTip" style="color: red;">
        单款有新的上传文件，请点击下方的“更新”按钮进行更新
      </div>
      <FormSubmitButtons
        :showSubmit="showSubmitBtn"
        :showUpdate="showUpdateBtn"
        size="medium"
        @save="$act(save)"
        @submit="$act(submit)"
        @update="$act(update)">
      </FormSubmitButtons>
    </div>
    <div class="form-container" v-if="order.deployWorkspace">
      <h3 style="padding-bottom: 20px">{{$t('order_management.form.product_info')}}</h3>
      <div class="flex">
        <div style="padding-right: 17px">Workspace</div>
        <div>{{order.deployWorkspace || $t('order_management.form.none')}}</div>
      </div>
    </div>
  </section>
</template>
<script>
import {
  createOrder, updateOrder, submitOrder, getOrderDetail,
} from '@/api/order-management';
import { getCompanyInfo } from '@/api/company';
import i18n from 'i18next';
import { getDictionaryByTypeCode } from '@/loaders/dictionary';
import dateFormatter from '@/filters/date.ts';
import OrderMaterials from '@/components/dynamic-form/OrderMaterials.vue';
import OrderSamples from '@/components/dynamic-form/OrderSamples.vue';
import Uploader from '@/components/dynamic-form/Uploader.vue';
import axios from '@/utils/axios';
import FormSubmitButtons from './FormSubmitButtons.vue';
import BasicForm from './BasicForm.vue';
import FormHeader from './FormHeader.vue';
import SuccessMessage from './SuccessMessage.vue';

export default {
  components: {
    BasicForm,
    FormHeader,
    FormSubmitButtons,
    OrderMaterials,
    OrderSamples,
    Uploader,
  },
  data() {
    return {
      order: {
        companyName: '',
        type: 1,
        date: '',
        time: '',
        form: [],
        attachment: [],
        status: null,
        remark: '',
        season: '',
        brand: '',
        category: '',
      },
      orderNo: '',
      company: {
        name: '',
      },
      options: [],
      loading: false,
    };
  },
  async mounted() {
    this.loading = true;
    await this.fetchCompanyInfo();
    await this.getOptions();
    this.order.companyName = this.company.name;
    // this.order.companyName = this.company.abbreviation || this.company.name;
    if (this.$route.query.orderNo) {
      this.orderNo = this.$route.query.orderNo;
      const res = (await getOrderDetail(this.orderNo)).data;
      this.transformData(res);
    }
    this.loading = false;
  },
  computed: {
    showUpdateTip() {
      if (this.order.status === null) return false;
      if (this.order.type !== 2) return false;
      if (!this.$refs.dynamicForm) return false;
      return !!this.$refs.dynamicForm.hasUpdated;
    },
    showSubmitBtn() {
      return this.order.status === null || this.order.status === 1;
    },
    showUpdateBtn() {
      return this.order.status !== null && this.order.status !== 1;
    },
    isOrderSubmited() {
      // return this.order.status && [2, 6].includes(this.order.status);
      return this.order.status && [6].includes(this.order.status);
    },
    labelWidth() {
      return i18n.language === 'en' ? '100px' : '90px';
    },
    remarkWidth() {
      return i18n.language === 'en' ? 'width: 1140px' : 'width: 1140px';
    },
    date() {
      return this.order.date;
    },
    time() {
      return this.order.time;
    },
    deliveryTime() {
      return this.order.deliveryTime;
    },
    type() {
      return this.order.type;
    },
  },
  watch: {
    date() {
      this.order.deliveryTime = new Date(`${this.order.date} ${this.order.time}`).getTime();
    },
    time() {
      this.order.deliveryTime = new Date(`${this.order.date} ${this.order.time}`).getTime();
    },
    async type() {
      if (!this.order.status) {
        this.order.form.splice(0, this.order.form.length);
        await this.$nextTick();
        this.$refs.dynamicForm.clearValidateForms();
      }
    },
  },
  methods: {
    transformData(data) {
      this.order = {
        ...data,
        date: dateFormatter(data.deliveryTime, 'YYYY-MM-DD'),
        time: dateFormatter(data.deliveryTime, 'HH:00'),
        form: data.form ? JSON.parse(data.form) : [],
        attachment: data.attachment ? JSON.parse(data.attachment) : [],
      };
    },
    getPureFormAttachment(order) {
      if (order.type !== 2) return order.form;
      const tmpForm = order.form.map(demandOrder => ({
        ...demandOrder,
        attachment: demandOrder.attachment.filter(
          i => i.files.length,
        ).length ? demandOrder.attachment : [],
      }));
      return tmpForm;
    },
    async save() {
      await this.validate();
      if (this.order.status === null) {
        await createOrder({ ...this.order, form: this.getPureFormAttachment(this.order) });
      } else if (this.order.status === 1) {
        await updateOrder({ ...this.order, orderNo: this.orderNo, form: this.getPureFormAttachment(this.order) });
      }
      this.goBack();
    },
    async update() {
      await this.validate();
      await updateOrder({ ...this.order, orderNo: this.orderNo, form: this.getPureFormAttachment(this.order) });
      this.goBack();
    },
    async submit() {
      if (this.order.status === null) {
        await this.validate();
        const orderNo = (
          await createOrder({ ...this.order, form: this.getPureFormAttachment(this.order) })
        ).data;
        this.orderNo = orderNo;
      }
      if (this.order.status === 1) {
        await this.validate();
        await updateOrder({
          ...this.order,
          orderNo: this.orderNo,
          form: this.getPureFormAttachment(this.order),
        });
      }
      await submitOrder({ orderNo: this.orderNo });
      await this.showSuccessMeesage().catch(e => { });
      this.goBack();
    },
    async validate() {
      await this.$refs.basicForm.validateForm();
      await this.$refs.dynamicForm.validateForms();
    },
    goBack() {
      this.$router.go(-1);
    },
    async showSuccessMeesage() {
      await this.$Dialog.dialogify({
        is: SuccessMessage,
      });
    },
    async fetchCompanyInfo() {
      try {
        this.company = (await getCompanyInfo()).data;
      } catch (e) {
        this.$errHandler(e);
      }
    },
    async getOptions() {
      const res = (await axios({
        url: '/operating/dict/getDictionaryTypeItem',
        method: 'get',
        params: {
          typeCode: 'materialType',
        },
        headers: {
          language: i18n.language,
        },
      })).data;
      const r = res.map((item) => {
        return {
          label: item.label,
          value: item.itemValue,
          key: item.itemValue,
        };
      });
      this.options = r;
    },
  },

};

</script>
<style lang="scss" scoped>
.uploader {
  /deep/ .tooltip-bar-wrapper {
    top: -34px !important;
  }
}
.order-form {
  width: 100%;
  flex-direction: column;
  flex: 1;
  padding-top: 44px;
  padding-bottom: 110px;
}

.form-container {
  width: 1200px;
  background: #fff;
  box-shadow: 0px 0px 10px 0px rgba(54, 74, 107, 0.1);
  border-radius: 8px;
  margin-top: 20px;
  padding: 30px;
}

.form-submit-section {
  width: 1200px;
  padding: 25px 30px;
  background: #fff;
  box-shadow: 0px 0px 10px 0px rgba(54, 74, 107, 0.1);
  border-radius: 8px;
  // margin-top: 20px;
  position: fixed;
  bottom: 0;
}

.split {
  width: 1140px;
  height: 1px;
  background: #dddddd;
  margin: 20px 0;
}
</style>
