













import { defineComponent, onMounted, ref } from '@vue/composition-api';
import i18n from 'i18next';

export default defineComponent({
  props: {
    cancelText: {
      type: String,
      default: i18n.t('file-list.back'),
    },
    title: {
      type: String,
      default: i18n.t('order_management.form.create_order'),
    },
    order: {
      type: Object,
    },
  },
  setup(props, { root }) {
    const routeName = ref<string | null | undefined>('');
    onMounted(() => {
      routeName.value = root.$route.name;
    });
    const goBack = () => {
      if (routeName.value === 'orderProgress') {
        root.$router.go(-1);
        return;
      }
      if (props.order && props.order.status && props.order.status === 6) {
        root.$router.go(-1);
        return;
      }
      if (root.$route.query.orderNo) {
        root.$router.go(-1);
        return;
      }
      root.$confirm(i18n.t('order_management.cancel_confirm.desc'),
        i18n.t('order_management.cancel_confirm.title'), {
          confirmButtonText: i18n.t('order_management.delete_confirm.confirm'),
          cancelButtonText: i18n.t('order_management.delete_confirm.cancel'),
          showClose: false,
        }).then(() => {
        root.$router.go(-1);
      }).catch(() => {});
    };
    return {
      goBack,
    };
  },
});
