  <div class="form-submit-buttons">
    <el-button
      v-if="showSubmit"
      :size="size"
      :style="btnStyle"
      round
      @click="$emit('submit')">{{ submitText }}</el-button>
    <el-button
      v-if="!showUpdate"
      :size="size"
      round
      :style="btnStyle"
      type="primary"
      @click="$emit('save')"
      class="button-save">{{ saveText }}</el-button>
    <el-button
      v-if="showUpdate"
      :size="size"
      :style="btnStyle"
      round
      type="primary"
      @click="$emit('update')">{{ updateText }}</el-button>

  </div>
</template>

<script>
import i18n from 'i18next';

export default {
  name: 'FormSubmitButtons',
  props: {
    size: {
      type: String,
      default: '',
    },
    btnStyle: {
      type: Object,
      default: () => ({}),
    },
    saveText: {
      type: String,
      default: i18n.t('order_management.form_submit.save'),
    },
    submitText: {
      type: String,
      default: i18n.t('order_management.form_submit.submit'),
    },
    updateText: {
      type: String,
      default: i18n.t('order_management.form_submit.update'),
    },
    showSubmit: {
      type: Boolean,
      default: true,
    },
    showUpdate: {
      type: Boolean,
      default: false,
    },
  },
};
</script>

